/* -----------------------------------------------------
   CSS Progress Bars
-------------------------------------------------------- */

.smartdown-container .smartdown-progress {
  width: 100%;
  height: 14px;
  display: none;
  background-color: #EEE;
}

.smartdown-container .smartdown-progress .smartdown-progress-bar {
  display: block;
  float: left;
  width: 0%;
  height: 100%;
  background: #3798d9;
  border-radius: 9px;
  transition: width 0.8s ease-in-out;
}

.smartdown-container .smartdown-progress .smartdown-progress-label {
  position: absolute;
  overflow: hidden;
  left: 0px;
  right: 0px;
  color: rgba(0, 0, 0, 0.6);
  font-size: 0.7em;
  text-align: center;
  text-shadow: 0px 1px rgba(0, 0, 0, 0.3);
}

.smartdown-container .smartdown-progress .smartdown-progress-active {
  background-image: linear-gradient(-45deg, rgba(255, 255, 255, 0.125) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.125) 50%, rgba(255, 255, 255, 0.125) 75%, transparent 75%, transparent);
  background-size: 35px 35px;
}

.smartdown-container .smartdown-progress .smartdown-progress-active {
  -webkit-animation: smartdown-progressActive 2s linear infinite;
  animation: smartdown-progressActive 2s linear infinite;
}

@-webkit-keyframes smartdown-progressActive {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 35px 35px;
  }
}

@keyframes smartdown-progressActive {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 35px 35px;
  }
}

